  .TodoItem {
    height: 45px;
    width: 90%;
    background-color: azure;
    position: relative;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 15px 15px 15px 15px;
  }

  .TodoItem-complete {
    height: 45px;
    width: 90%;
    background-color: rgba(32, 35, 41, 0.5);
    position: relative;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    border-radius: 15px 15px 15px 15px;
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {    
    text-decoration: line-through;
    
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
  }
  .Icon-check--active {
    color: #4caf50;
  }
  
  .Icon-delete {
    position: absolute;
    top: 0;
    right: 0;
  }
  .Icon-delete:hover {
    color: red;
  }